* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  
  :root {
    --first-color: #ff005a;  // Bright Pink
    --second-color: #c9b737; // Purple
    --third-color: #0e0e0e;     // Black
    --body-front: "Montserrat", sans-serif;
  }
  
  .body404 {
    margin: 0;
    font-family: var(--body-front);
    background-color: var(--third-color);
  }
  
  #notfound {
    position: relative;
    height: 100vh;
  }
  
  #notfound .notfound {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    max-width: 767px;
    width: 100%;
    line-height: 1.4;
    text-align: center;
  }
  
  .notfound .notfound-404 {
    position: relative;
    height: 180px;
    margin-bottom: 20px;
    font-size: 200px;
    z-index: -1;
  }
  
  #h1404 {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    font-size: 224px;
    font-weight: 900;
    margin: 0 0 0 -12px;
    color: var(--third-color);
    text-transform: uppercase;
    text-shadow: -1px -1px 0px var(--second-color), 1px 1px 0px var(--first-color);
    letter-spacing: -20px;
  }
  
  #h2404 {
    position: absolute;
    left: 0;
    top: 110px;
    right: 0;
    font-size: 42px;
    font-weight: 700;
    color: #c9b737;  // Yellow
    text-transform: uppercase;
    text-shadow: -1px -1px 0px var(--first-color);
    letter-spacing: 13px;
    margin: 0;
  }
  
  #errorbutton {
    display: inline-block;
    text-transform: uppercase;
    color: var(--first-color);
    text-decoration: none;
    border: 2px solid;
    background: transparent;
    padding: 10px 40px;
    font-size: 14px;
    font-weight: 700;
    margin: 15px;
    border-radius: 5px;
    -webkit-transition: 0.2s all;
    -ms-transition: 0.2s all;
    transition: 0.2s all;
  }
  
  #errorbutton:hover {
    color: var(--second-color);
  }
  
  @media (max-width: 767px) {
    #h2404 {
      font-size: 24px !important;
    }
  }
  
  @media (max-width: 480px) {
    #h1404 {
      font-size: 182px !important;
    }
  }